import React, { useReducer, useContext, Fragment } from 'react';
import PropTypes from 'prop-types';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import Popover from '@material-ui/core/Popover';
import PictureDetailsPopUp from './PictureDetailsPopUp';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import {
  openPictureDetailsPopUp,
  closePictureDetailsPopUp,
  handlePictureTitleVisability,
} from './../../actions/collection';
import collectionStyles from './CollectionStyles';
import CollectionContext from '../../context/collection-context';

const theme = createMuiTheme({
  typography: {
    useNextVariants: true,
  },
  overrides: {
    MuiPopover: {
      paper: {
        top: '50% !important',
        left: '50% !important',
        width: '800px',
        height: '470px',
        marginLeft: '-400px',
        marginTop: '-300px',
      },
    },
  },
});

const initialState = {
  isHovered: false,
  popUpTarget: null,
};

const reducer = (state, action) => {
  switch (action.type) {
    case 'HOVER_ON':
      return { ...state, isHovered: true };
    case 'HOVER_OUT':
      return { ...state, isHovered: false };
    case 'OPEN_PICTURE_DETAILS_POP_UP':
      return { ...state, popUpTarget: action.currentTarget };
    case 'CLOSE_POP_UP':
      return { ...state, popUpTarget: null };
    default:
      return initialState;
  }
};

const Picture = ({
  classes,
  picture,
  openPictureDetailsPopUp,
  closePictureDetailsPopUp,
  handlePictureTitleVisability,
  isPictureTitleShown,
}) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const context = useContext(CollectionContext);

  const pictureTitle = state.isPictureTitleShown ? classes.titleIsVisible : classes.titleIsHidden;
  const image = picture.hasImage ? (
    <img src={picture.headerImage.url} alt={picture.title} className={classes.pictureImage} />
  ) : (
    <div className={classes.noImage}>No image</div>
  );

  const handleOpenPopUp = (event) => {
    openPictureDetailsPopUp({ selectedPicture: picture, popUpTarget: event.currentTarget });
    //    context.handleBlurContent();
  };

  const handleClosePopUp = () => {
    closePictureDetailsPopUp(picture);
    //context.handleBlurContent();
  };

  return (
    <Fragment>
      <div
        variant="contained"
        className={classes.pictureContainer}
        onClick={(event) => handleOpenPopUp(event)}
        onMouseEnter={handlePictureTitleVisability}
        onMouseLeave={handlePictureTitleVisability}
      >
        {image}
        <GridListTileBar className={pictureTitle} title={picture.longTitle} />
      </div>

      <MuiThemeProvider theme={theme}>
        <Popover
          open={Boolean(picture.popUpTarget)}
          anchorEl={picture.popUpTarget}
          anchorOrigin={{
            vertical: 'center',
            horizontal: 'center',
          }}
          transformOrigin={{
            vertical: 'center',
            horizontal: 'center',
          }}
        >
          <PictureDetailsPopUp picture={picture} handleClosePictureDetails={handleClosePopUp} />
        </Popover>
      </MuiThemeProvider>
    </Fragment>
  );
};

Picture.propTypes = {
  classes: PropTypes.object.isRequired,
  picture: PropTypes.object,
  handleBlurContent: PropTypes.func,
  onClick: PropTypes.func,
};
const mapStateToProps = (state) => {
  return {
    isPictureTitleShown: state.collection.isPictureTitleShown,
    //pageSize: state.collection.pageSize,
    //queryParam: state.collection.queryParam,
    //orderByParam: state.collection.orderByParam,
    //isPageSizeListOpened: state.collection.isPageSizeListOpened,
  };
};
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      openPictureDetailsPopUp,
      closePictureDetailsPopUp,
      handlePictureTitleVisability,
    },
    dispatch
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(collectionStyles)(Picture));
