import React, { memo } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import App from './App';
import PictureDetails from './PictureDetails/PictureDetails';

const Router = () => (
  <BrowserRouter>
    <Switch>
      <Route exact path="/:objectType?" render={() => <App />} />
      <Route exact path="/picture/:objectNumber" render={() => <PictureDetails />} />
    </Switch>
  </BrowserRouter>
);

export default Router;
