import {
  FETCH_COLLECTION_REQUEST,
  FETCH_COLLECTION_SUCCESS,
  FETCH_COLLECTION_FAILURE,
  HANDLE_PAGE_SIZE,
  HANDLE_PAGE_NUMBER,
  HANDLE_SEARCH,
  HANDLE_ORDER,
  HANDLE_SELECT_PAGE_SIZE_VISABILITY,
  OPEN_PICTURE_DETAILS_POP_UP,
  CLOSE_PICTURE_DETAILS_POP_UP,
  HANDLE_PICTURE_TITLE_VISABILITY,
} from './../constants/actionTypes';

const initialState = {
  error: null,
  pictureList: [],
  isLoading: false,
  pageNumber: 1,
  pageSize: 50,
  queryParam: '',
  orderByParam: '',
  isPageSizeListOpened: false,
  isPictureTitleShown: false,
};

const removePopUpTargetFromSelectedPicture = (state, action) => {
  const newState = {
    ...state,
    pictureList: {
      ...state.pictureList,
      artObjects: [...state.pictureList.artObjects],
    },
  };

  const selectedPicture = newState.pictureList.artObjects.find((picture) => {
    return picture.id === action.selectedPicture.id;
  });
  selectedPicture.popUpTarget = null;

  return newState;
};

const addPopUpTargetToSelectedPicture = (state, action) => {
  console.log(action);
  return {
    ...state,
    pictureList: {
      ...state.pictureList,
      artObjects: state.pictureList.artObjects.map((picture) => {
        let selectedPicture = action.payload.selectedPicture;
        selectedPicture['popUpTarget'] = action.payload.popUpTarget;
        return picture.id === selectedPicture.id ? selectedPicture : picture;
      }),
    },
  };
};

const collection = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_COLLECTION_REQUEST:
      return { ...state, isLoading: true, error: null };
    case FETCH_COLLECTION_SUCCESS:
      return { ...state, pictureList: action.pictureList, isLoading: false };
    case FETCH_COLLECTION_FAILURE:
      return { ...state, error: action.error };
    case HANDLE_PAGE_SIZE:
      return { ...state, pageSize: action.pageSize };
    case HANDLE_PAGE_NUMBER:
      return { ...state, pageNumber: action.pageNumber };
    case HANDLE_SEARCH:
      return { ...state, queryParam: action.queryParam };
    case HANDLE_ORDER:
      return { ...state, orderByParam: action.orderByParam };
    case HANDLE_SELECT_PAGE_SIZE_VISABILITY:
      return { ...state, isPageSizeListOpened: !state.isPageSizeListOpened };
    case OPEN_PICTURE_DETAILS_POP_UP:
      return addPopUpTargetToSelectedPicture(state, action);
    case CLOSE_PICTURE_DETAILS_POP_UP:
      return removePopUpTargetFromSelectedPicture(state, action);
    case HANDLE_PICTURE_TITLE_VISABILITY:
      return { ...state, isPictureTitleShown: !state.isPictureTitleShown };

    default:
      return state;
  }
};
export default collection;
