import {
  FETCH_COLLECTION_REQUEST,
  FETCH_COLLECTION_SUCCESS,
  FETCH_COLLECTION_FAILURE,
  HANDLE_PAGE_SIZE,
  HANDLE_PAGE_NUMBER,
  HANDLE_SEARCH,
  HANDLE_ORDER,
  HANDLE_SELECT_PAGE_SIZE_VISABILITY,
  OPEN_PICTURE_DETAILS_POP_UP,
  CLOSE_PICTURE_DETAILS_POP_UP,
  HANDLE_PICTURE_TITLE_VISABILITY,
} from '../constants/actionTypes';

const fetchCollectionRequest = () => {
  return {
    type: FETCH_COLLECTION_REQUEST,
  };
};

const fetchCollectionSuccess = (pictureList) => {
  return {
    type: FETCH_COLLECTION_SUCCESS,
    pictureList,
  };
};

const fetchCollectionFailed = (error) => {
  return {
    type: FETCH_COLLECTION_FAILURE,
    error,
  };
};

export const HandlePageSize = (pageSize) => (dispatch) => {
  dispatch({ type: HANDLE_PAGE_SIZE, pageSize });
};

export const handlePageNumber = (pageNumber) => (dispatch) => {
  dispatch({ type: HANDLE_PAGE_NUMBER, pageNumber });
};

export const openPictureDetailsPopUp = (payload) => (dispatch) => {
  dispatch({ type: OPEN_PICTURE_DETAILS_POP_UP, payload });
};

export const closePictureDetailsPopUp = (selectedPicture) => (dispatch) => {
  dispatch({ type: CLOSE_PICTURE_DETAILS_POP_UP, selectedPicture });
};

export const handleSelectPageSizeVisability = () => (dispatch) => {
  dispatch({ type: HANDLE_SELECT_PAGE_SIZE_VISABILITY });
};

export const handlePictureTitleVisability = () => (dispatch) => {
  dispatch({ type: HANDLE_PICTURE_TITLE_VISABILITY });
};

export const fetchCollection = (pageSize = 50, pageNumber = 1, orderByParam = '', queryParam = '') => {
  const baseUrl = 'https://www.rijksmuseum.nl/api/en/collection?key=nMG7xRY4&format=json&';
  const getCollectionEndpoint = `${baseUrl}ps=${pageSize}&p=${pageNumber}&s=${orderByParam}&q=${queryParam}`;

  return (dispatch) => {
    dispatch(fetchCollectionRequest());

    fetch(getCollectionEndpoint)
      .then((response) => response.json())
      .then((pictureList) => {
        dispatch(fetchCollectionSuccess(pictureList));
      })
      .catch((error) => {
        dispatch(fetchCollectionFailed(error));
      });
  };
};
